import {   Grid, Paper } from "@mui/material";
import {  useEffect } from "react";
import AppPagination from "../../app/components/AppPagination";
import CheckboxButton from "../../app/components/CheckboxButton";
import RadioButtonGroup from "../../app/components/RadioButtonGroup";
import LoadingComponents from "../../app/layout/LoadingComponents";
import { useAppDispatch, useAppSelector } from "../../app/store/configureStore";
import { fetchFilters, fetchUrunlerAsync, setPageNumber, setUrunParams, urunSelectors } from "./categorySlice";
import UrunList from "./UrunList";
import UrunSearch from "./UrunSearch";

const sortOptions=[
  {value:'name', label:'Alfabetik'},
  {value:'fiyatDesc', label:'Fiyat oktan-Aza'},
  {value:'fiyat', label:'Fiyat Azdan-oa'},
]

export default function Category(){
  const uruns=useAppSelector(urunSelectors.selectAll);
  const dispatch=useAppDispatch();
  const {urunLoaded,filtersLoaded,marka,type,urunParams,metaData}=useAppSelector(state=>state.category);
useEffect(()=>{
  if(!urunLoaded) dispatch(fetchUrunlerAsync());

    }, [urunLoaded,dispatch])

    useEffect(()=>{
      if(!filtersLoaded) dispatch(fetchFilters());

    },[dispatch,filtersLoaded])


if(!filtersLoaded ) return <LoadingComponents/>

 
  return(
    <Grid container columnSpacing={4}>
       <Grid item xs={3}>
        <Paper sx={{mb:2}}>
          <UrunSearch/>
        </Paper>
        <Paper sx={{mb:2,p:2}}>
      
       <RadioButtonGroup 
       selectedValue={urunParams.orderBy}//alfabetik oktan aza azdan oa 
       options={sortOptions}
       onChange={(e)=>dispatch(setUrunParams({orderBy:e.target.value}))}
       />
        </Paper>

        <Paper sx={{mb:2,p:2}}>
        <CheckboxButton
                        items={marka}
                        checked={urunParams.marka}
                        onChange={(items: string[]) => dispatch(setUrunParams({ marka: items }))}
                    />
                </Paper>
                <Paper sx={{ mb: 2, p: 2 }}>
                    <CheckboxButton
                        items={type}
                        checked={urunParams.type}
                        onChange={(items: string[]) => dispatch(setUrunParams({ type: items }))}
                    />
        </Paper>
       </Grid>
       <Grid item xs={9}>
       <UrunList  uruns={uruns} />
       </Grid>
       <Grid item xs={3}/>
       <Grid item xs={9} sx={{mb:2}}>
        {metaData &&
       <AppPagination 
       metaData={metaData}
       onPageChange={(page:number)=>dispatch(setPageNumber({pageNumber:page}))}
       />}
        </Grid>   
   </Grid>
   
  )  
}